<template>
  <div>window.open的新窗口页面的参数 {{ urlQuery }}</div>
</template>

<script>
export default {
  data() {
    return {
      urlQuery: null,
    };
  },

  created() {
    var str = location.href; //取得整个地址栏
    var num = str.indexOf("?");
    str = str.substr(num + 1); //取得所有参数
    this.urlQuery = str;
  },
};
</script>