<template>
  <div>
    <div class="help">
      <div style="text-align:center">
        <span>ck总数：{{ ZqdyjInData.totalCount }}</span>&nbsp;&nbsp;
        <span>ck火爆：{{ ZqdyjInData.hotCount }}</span>&nbsp;&nbsp;
        <span>无助力：{{ ZqdyjInData.noHelp }}</span>&nbsp;&nbsp;
        <span>有助力：{{ ZqdyjInData.hasHelp }}</span>
      </div>
      <div style="text-align:center;margin: 10px;">赚钱大赢家</div>
      <el-form :model="zqdyjForm" ref="zqdyjForm" :rules="rules" label-width="100px">
        <el-form-item label="账号" prop="account">
          <el-input v-model="zqdyjForm.account" clearable placeholder="请输入ck或者助力码"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="zqdyjForm.remark" clearable placeholder="请输入备注"></el-input>
        </el-form-item>
        <el-form-item label="是否定时" prop="isTime">
          <el-switch v-model="zqdyjForm.isTime"></el-switch>
        </el-form-item>
      </el-form>
      <div style="margin-top:10px;text-align: center;">
        <el-button type="primary" :loading="goLoading" @click="GO('zqdyjForm')">GO</el-button>
      </div>
    </div>

    <div class="log">
      <div style="text-align:center;margin: 10px;">日志</div>
      <div class="msg">
        <div v-for="(log, index) in logs" :key="index">
          <span v-if="log.indexOf('助力成功') != -1" style="color:greenyellow">{{ log }}</span>
          <span v-else>{{ log }}</span>
        </div>
      </div>
    </div>

    <div class="assistList">
      <div style="text-align:center;font-size:18px;font-weight:600">助力列表</div>
      <el-table :data="assistList" style="width: 100%">
        <el-table-column prop="remark" label="名字(备注)" align="center"></el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scoped">{{scoped.row.status | status}}</template>
        </el-table-column>
        <el-table-column label="助力" align="center">
          <template slot-scope="scoped">{{scoped.row.helpStatus | helpStatus}}</template>
        </el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scoped">
            <el-button
              type="text"
              :loading="scoped.row.deleteLoading"
              @click="deleteLine(scoped.row.id)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  zqdyjHelp,
  getZqdyjData,
  getZqdyjdelete
} from "../axiosRequest/allRequest";
export default {
  filters: {
    status(value) {
      const obj = {
        0: "有效",
        1: "过期"
      };
      return obj[value];
    },
    helpStatus(value) {
      const obj = {
        0: "未满",
        1: "已满"
      };
      return obj[value];
    }
  },
  data() {
    return {
      zqdyjForm: {
        account: "",
        remark: "",
        isTime: false
      },
      logs: [],
      goLoading: false,
      websock: null,
      getZqdyjIn: null,
      ZqdyjInData: {},
      assistList: [],
      rules: {
        account: [
          { required: true, message: "请输入ck或者助力码", trigger: "blur" }
        ],
        remark: [{ required: true, message: "请输入备注", trigger: "blur" }],
        isTime: []
      }
    };
  },

  created() {
    this.initWebSocket();
  },
  mounted() {
    getZqdyjData().then(res => {
      if (res.code == 200) {
        this.ZqdyjInData = res.data;
        let a = JSON.parse(JSON.stringify(res.data.helpList));
        a.forEach((item, index) => {
          this.$set(item, "deleteLoading", false);
        });
        this.assistList = a;
      }
    });
    this.getZqdyjIn = setInterval(() => {
      getZqdyjData().then(res => {
        if (res.code == 200) {
          this.ZqdyjInData = res.data;
          let a = JSON.parse(JSON.stringify(res.data.helpList));
          a.forEach((item, index) => {
            this.$set(item, "deleteLoading", false);
          });
          this.assistList = a;
        }
      });
    }, 60 * 1000);
  },
  destroyed() {
    this.websock.close(); //离开路由之后断开websocket连接
    clearInterval(this.getZqdyjIn);
    this.getZqdyjIn = null;
  },

  methods: {
    GO(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.logs = [];
          this.goLoading = true;
          zqdyjHelp(this.zqdyjForm).then(res => {
            this.goLoading = false;
            if (res.code != 200) {
              Message.error(res.message);
            } else {
              Message.success(res.message);
              getZqdyjData().then(res => {
                if (res.code == 200) {
                  this.ZqdyjInData = res.data;
                  let a = JSON.parse(JSON.stringify(res.data.helpList));
                  a.forEach((item, index) => {
                    this.$set(item, "deleteLoading", false);
                  });
                  this.assistList = a;
                }
              });
            }
          });
        } else {
          return false;
        }
      });
    },

    deleteLine(id) {
      this.assistList.forEach((item, index) => {
        if ((item.id = id)) {
          this.$set(item, "deleteLoading", true);
        }
      });
      getZqdyjdelete({ id: id }).then(res => {
        this.assistList.forEach((item, index) => {
          if ((item.id = id)) {
            this.$set(item, "deleteLoading", false);
          }
        });
        if (res.code == 200) {
          Message.success(res.message);
          getZqdyjData().then(res => {
            if (res.code == 200) {
              this.ZqdyjInData = res.data;
              let a = JSON.parse(JSON.stringify(res.data.helpList));
              a.forEach((item, index) => {
                this.$set(item, "deleteLoading", false);
              });
              this.assistList = a;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },

    initWebSocket() {
      //初始化weosocket
      let token = window.sessionStorage.getItem("robotToken");
      // debugger
      let ipport = window.location.host;
      const wsuri = "ws://" + ipport + "/robot/ZqdyjWebSocket/" + token;
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      // this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      // this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //连接建立之后执行send方法发送数据
    },
    websocketonerror() {
      //连接建立失败重连
      this.initWebSocket();
    },
    websocketonmessage(e) {
      //数据接收
      // debugger
      this.logs.push(e.data);
    },
    websocketsend(Data) {
      //数据发送
      this.websock.send(Data);
    },
    websocketclose(e) {
      //关闭
      console.log("断开连接", e);
    }
  }
};
</script>

<style lang="less" scoped>
.help {
  width: 30%;
  background-color: rgba(255, 255, 255, 0.5);
  margin: auto;
  //   margin-top: 5%;
  padding: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.log {
  width: 30%;
  background-color: rgba(255, 255, 255, 0.5);
  margin: auto;
  margin-top: 5%;
  padding: 10px;
  border-radius: 10px;

  .msg {
    height: 50vh;
    overflow-y: auto;
  }
}

::v-deep .el-input__inner {
  border: 1px solid black;

  &::-webkit-input-placeholder {
    color: rgb(98, 97, 97);
  }

  .el-input__suffix {
    color: black;
  }
}

::v-deep .el-form-item__label {
  color: black;
}

.logs {
  width: 100%;
  height: 400px;
  background-color: pink;
  overflow: hidden;
}

.assistList {
  position: fixed;
  top: 61px;
  right: 20px;
  width: 400px;
  box-shadow: 2px 2px 5px #888888;
}
</style>