<template>
  <div>
    <div style="background-color: aqua">父子组件传值</div>
    <el-button type="primary" @click="getFatherData"
      >父组件中的FatherData+1</el-button
    >
    <el-button type="primary" @click="getFatherDatao"
      >父组件中的FatherData置0</el-button
    >

    <!-- 子组件注册的名字即为子组件标签名 -->
    <!-- 传值用v-model  :  -->
    <!-- 子组件传值给父组件   在子组件中使用this.$emit方法定义方法名和传的值即可 -->
    <Son :FatherData="FatherData" @changeData="changeData"></Son>

    <div style="background-color: aqua">插入DOM元素</div>
    <div class="Father" id="FatherDiv" ref="FatherDiv">
      <el-button @click="pushDiv">插入新的DIV</el-button>
      <div>原本存在的DIV</div>
    </div>

    <div style="background-color: aqua">js创建一个对象</div>
    <div>
      <el-button @click="createNewObj">创建对象</el-button>
      <div>创建的对象：{{ createObj }}</div>
    </div>

    <div style="background-color: aqua">window.open传参</div>
    <div>
      <el-input
        v-model="canshuName"
        placeholder="请输入要传递的参数名"
        clearable
      ></el-input>
      <el-input
        v-model="canshuValue"
        placeholder="请输入要传递的参数值"
        clearable
      ></el-input>
      <el-button @click="openNew">window.open</el-button>
    </div>
  </div>
</template>

<script>
import Son from "./Son.vue"; //引入子组件
export default {
  data() {
    return {
      FatherData: 1,
      createObj: null,
      canshuName: "",
      canshuValue: "",
    };
  },
  components: {
    Son, //注册子组件
  },
  methods: {
    getFatherData() {
      this.FatherData++;
    },
    getFatherDatao() {
      this.FatherData = 0;
    },
    changeData(data) {
      this.FatherData = data;
    },

    //插入DON元素
    pushDiv() {
      let oldDiv = document.getElementById("FatherDiv"); //获取到父节点
      let newDiv = document.createElement("div"); //创建新的div元素
      newDiv.innerText = "新增的div"; //div元素里的内容
      //   this.$refs.FatherDiv.appendChild(newDiv); //vue中可以通过ref名称获取到元素
      oldDiv.appendChild(newDiv); //在父元素最后添加节点
    },
    //创建对象
    createNewObj() {
      let newobj = new Object(); //new关键字调用构造器创建对象
      newobj.name = "zm";
      newobj.year = "18";
      this.createObj = newobj;
    },
    //window.open
    openNew() {
      window.open(
        "http://localhost:6300/#/newOpen?" +
          this.canshuName +
          "=" +
          this.canshuValue
      );
    },
  },
};
</script>

<style lang="less" scoped>
.Father {
  background-color: rgb(90, 90, 247);
  min-height: 50px;
}
</style>