<script lang="ts">
import { Message } from "element-ui";
import { queryVersion } from "../axiosRequest/allRequest";
export default {
  data() {
    return {
      indexActive: "AddQlong",
      curVersion: "", //当前版本
      newestVersion: "" //最新版本
    };
  },
  created() {
    let a = sessionStorage.getItem("pathRouter");
    if (a) {
      this.indexActive = a;
    }
    sessionStorage.setItem("pathRouter", this.indexActive);
    this.getQueryVersion();
  },
  methods: {
    handleOpen(index) {
      this.indexActive = index;
      sessionStorage.setItem("pathRouter", this.indexActive);
    },
    logOut() {
      this.$confirm("是否确认退出当前登录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        sessionStorage.clear();
        this.$router.replace("/htgl");
      });
    },
    getQueryVersion() {
      queryVersion().then((res) => {
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          this.curVersion = res.data.curVersion;
          this.newestVersion = res.data.newestVersion;
        }
      });
    },
  }

};
</script>

