<template>
  <div>
    <div>子组件里接收到的数据：{{ FatherData }}</div>
    <el-button @click="changeFatherData">子组件中的button</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  //props用于接收父组件中传来的值  必须和父组件中子组件标签绑定过的变量名一样
  props: {
    FatherData: Number, //必须定义接收的值得类型  接收到的值等同于data中定义的变量 可以直接拿来用
  },
  methods: {
    changeFatherData() {
      this.$emit("changeData", 11111); //改变父组件值得方法
    },
  },
};
</script>

<style lang="less" scoped>
</style>