<template src="./echartsTset.html"></template>
    
<script>
import * as echarts from "echarts";
import { Message } from "element-ui";
export default {
  data() {
    return {
      form: {},
      option: {
        xAxis: {
          type: "category",
          data: []
        },
        yAxis: {
          type: "value"
        },
        series: [
          {
            data: [],
            type: "bar",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)"
            }
          }
        ]
      }
    };
  },
  methods: {
    onSubmit() {
      if (JSON.stringify(this.form) == "{}")
        return Message.warning("请填写至少一组数据");
      this.option.xAxis.data = [];
      this.option.series[0].data = [];
      for (let i in this.form) {
        if (i.indexOf("title") != -1) {
          this.option.xAxis.data.push(this.form[i]);
        }
        if (i.indexOf("value") != -1) {
          this.option.series[0].data.push(this.form[i]);
        }
      }
      var myChart = echarts.init(document.getElementById("echartsBox"));
      myChart.setOption(this.option);
    }
  }
};
</script>

<style lang="less" scoped src="./echartsTest.less"></style>