<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">青龙列表</div>
      <div class="rightButton">
        <el-button type="primary" size="mini" @click="ckAuto" :loading="ckLoading">ck迁移</el-button>
        <el-button type="primary" size="mini" @click="addNewQl('添加青龙')">添加青龙</el-button>
        <el-button type="primary" size="mini" :loading="bigHeadLoading" @click="bigHead()">一键大车头</el-button>
        <el-button
          type="primary"
          size="mini"
          :loading="cancelBigHeadLoading"
          @click="cancelBigHead()"
        >取消大车头</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <el-table
        :data="qlListData"
        show-summary
        :summary-method="getSummaries"
        style="width: 100%"
        v-loading="qlListLoading"
      >
        <el-table-column type="index" label="序号" width="80"></el-table-column>
        <el-table-column prop="url" label="青龙地址" width align="center"></el-table-column>
        <el-table-column prop="head" label="车头" width align="center"></el-table-column>
        <el-table-column prop="curFirst" label="首位" width align="center"></el-table-column>
        <el-table-column prop="validCount" label="有效ck数" width align="center"></el-table-column>
        <el-table-column prop="invalidCount" label="无效ck数" width align="center"></el-table-column>
        <el-table-column prop="count" label="ck数" width align="center"></el-table-column>
        <el-table-column prop="remark" label="备注" width align="center"></el-table-column>
        <el-table-column prop="status" label="status" width="100" align="center"></el-table-column>
        <el-table-column label="操作" fixed="right" width="200" align="center">
          <template slot-scope="scoped">
            <el-button type="text" style="color: blue" @click="recoveryQl(scoped.row)">恢复</el-button>
            <el-button type="text" style="color: blue" @click="addNewQl('修改青龙', scoped.row)">修改</el-button>
            <el-button
              type="text"
              style="color: blue"
              :loading="scoped.row.deleteLoading"
              @click="deleteQl(scoped.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 添加青龙 -->
    <el-dialog
      :title="qlBoxTitle"
      :visible.sync="qlVisible"
      width="30%"
      @close="qlClosed('qlForm')"
    >
      <el-form :model="qlForm" :rules="qlFormRules" ref="qlForm">
        <el-form-item label="青龙地址" prop="url">
          <el-input v-model="qlForm.url" clearable placeholder="请输入url"></el-input>
        </el-form-item>
        <el-form-item label="clientID" prop="clientID">
          <el-input v-model="qlForm.clientID" clearable placeholder="请输入clientID"></el-input>
        </el-form-item>
        <el-form-item label="clientSecret" prop="clientSecret">
          <el-input v-model="qlForm.clientSecret" clearable placeholder="请输入clientSecret"></el-input>
        </el-form-item>
        <el-form-item label="小车头" prop="head">
          <el-input v-model="qlForm.head" clearable placeholder="请输入车头"></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="qlForm.remark" clearable placeholder="请输入备注"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="qlVisible = false">取 消</el-button>
        <el-button type="primary" :loading="qlLoading" @click="sureChangeQl('qlForm')">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 恢复青龙 -->

    <el-dialog title="恢复" :visible.sync="recoveryVisible" width="30%">
      <el-select v-model="oldQl" placeholder="请选择">
        <el-option v-for="(item, index) in qlRecoverList" :key="index" :label="item" :value="item"></el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="recoveryVisible = false">取 消</el-button>
        <el-button type="primary" @click="recoverySure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  queryQl,
  addQl,
  updateQl,
  deleteQls,
  oneKeyHead,
  cancelHead,
  getRecoveryQl,
  recoveryCk,
  autoAdjust
} from "../axiosRequest/allRequest";
export default {
  data() {
    return {
      searchForm: {
        url: ""
      },
      qlListData: [],
      qlBoxTitle: "添加青龙",
      qlVisible: false,
      qlForm: {
        url: "",
        clientID: "",
        clientSecret: "",
        head: "",
        validCount: 0,
        invalidCount: 0,
        count: 0,
        curFirst: "",
        remark: ""
      },
      qlFormRules: {
        url: [{ required: true, message: "请输入url", trigger: "blur" }],
        clientID: [
          { required: true, message: "请输入clientID", trigger: "blur" }
        ],
        clientSecret: [
          { required: true, message: "请输入clientSecret", trigger: "blur" }
        ],
        head: [
          // { required: true, message: '请输入车头', trigger: 'blur' },
        ],
        remark: [{ required: true, message: "请输入备注", trigger: "blur" }]
      },
      qlListLoading: false,
      qlLoading: false,
      bigHeadLoading: false,
      cancelBigHeadLoading: false,
      recoveryVisible: false,
      oldQl: "",
      newQl: "",
      qlRecoverList: [],
      ckLoading: false
    };
  },

  created() {
    this.refushQl();
  },

  mounted() {},
  methods: {
    recoveryQl(row) {
      this.recoveryVisible = true;
      this.newQl = row.remark;
      getRecoveryQl().then(res => {
        if (res.code == 200) {
          this.qlRecoverList = res.data;
        } else {
          Message.error(res.message);
        }
      });
    },

    recoverySure() {
      let param = {
        oldQl: this.oldQl,
        newQl: this.newQl
      };

      recoveryCk(param).then(res => {
        if (res.code == 200) {
          this.recoveryVisible = false;
          this.refushQl();
        } else {
          Message.error(res.message);
        }
      });
    },

    refushQl() {
      this.qlListData = [];
      this.qlListLoading = true;
      let qlUrl = {
        url: this.searchForm.url
      };
      queryQl(qlUrl)
        .then(res => {
          this.qlListLoading = false;
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            let a = JSON.parse(JSON.stringify(res.data));
            a.forEach((item, index) => {
              this.$set(item, "deleteLoading", false);
            });
            this.qlListData = a;
          }
        })
        .catch(() => {
          this.qlListLoading = false;
        });
    },

    addNewQl(val, row) {
      this.qlVisible = true;
      this.qlBoxTitle = val;
      if (row) {
        //回填row
        this.qlForm.url = row.url;
        this.qlForm.clientID = row.clientID;
        this.qlForm.clientSecret = row.clientSecret;
        this.qlForm.head = row.head;
        this.qlForm.remark = row.remark;
        this.qlForm.id = row.id;
      } else {
        this.qlForm = {};
      }
    },
    qlClosed(formName) {
      this.$refs[formName].resetFields();
    },
    sureChangeQl(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.qlLoading = true;
          if (this.qlBoxTitle == "添加青龙") {
            addQl(this.qlForm).then(res => {
              this.qlLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.qlVisible = false;
                this.refushQl();
              }
            });
          } else if (this.qlBoxTitle == "修改青龙") {
            updateQl(this.qlForm).then(res => {
              this.qlLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success(res.message);
                this.qlVisible = false;
                this.refushQl();
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    deleteQl(row) {
      this.$confirm("确认删除此青龙数据吗？", "确认删除", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.qlListData.forEach((item, index) => {
          if (item.id == row.id) {
            this.$set(item, "deleteLoading", true);
          }
        });
        deleteQls({ ids: row.id }).then(res => {
          this.qlListData.forEach((item, index) => {
            if (item.id == row.id) {
              this.$set(item, "deleteLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success(res.message);
            this.refushQl();
          }
        });
      });
    },
    bigHead() {
      this.bigHeadLoading = true;
      oneKeyHead().then(res => {
        this.bigHeadLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
          this.refushQl();
        } else {
          Message.success(res.message);
          this.refushQl();
        }
      });
    },
    cancelBigHead() {
      this.cancelBigHeadLoading = true;
      cancelHead().then(res => {
        this.cancelBigHeadLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
          this.refushQl();
        } else {
          Message.success(res.message);
          this.refushQl();
        }
      });
    },
    async ckAuto() {
      this.ckLoading = true;
      const data = await autoAdjust();
      this.ckLoading = false;
      if (data.code == 200) {
        Message.success("迁移成功！");
        this.refushQl();
      } else {
        Message.error(data.message);
      }
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        if (index === 4 || index === 5 || index === 6) {
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
            sums[index];
          }
        }
      });

      return sums;
    }
  }
};
</script>

<style lang="less" scoped>
</style>