<template>
  <div>
    <div class="topTitle">
      <div class="leftTitle">助力池</div>
      <div class="rightButton">
        <!-- <el-input v-model="searchForm.url" placeholder="url" clearable></el-input> -->
        <el-button type="primary" size="mini" @click="addCk(1)">添加ck</el-button>
      </div>
    </div>

    <div class="mainMseeage">
      <div class="cardBox">
        <el-card class="box-card" shadow="always">
          <div slot="header" class="clearfix">
            <div>东东农场</div>
            <div v-if="zlcList.fruit.run">
              <i class="el-icon-refresh" style="color:#66ff03"></i>运行中
            </div>
            <div v-else>
              <i class="el-icon-remove-outline" style="color:yellow"></i>未运行
            </div>
          </div>
          <div class="cardT">
            <div>
              <div>火爆：{{zlcList.fruit.fruitHot}}</div>
              <div>已助力：{{zlcList.fruit.fruitHasHelp}}</div>
              <div>有助力：{{zlcList.fruit.fruitHaveHelp}}</div>
            </div>
            <div>
              <el-button
                type="primary"
                :disabled="zlcList.fruit.run"
                :loading="fruitLoading"
                @click="fruitRun"
              >开始助力</el-button>
              <el-button type="success" @click="reset(0)">火爆重置</el-button>
            </div>
          </div>
        </el-card>
        <el-card class="box-card" shadow="always">
          <div slot="header" class="clearfix">
            <div>东东萌宠</div>
            <div v-if="zlcList.pet.run">
              <i class="el-icon-refresh" style="color:#66ff03"></i> 运行中
            </div>
            <div v-else>
              <i class="el-icon-remove-outline" style="color:yellow"></i> 未运行
            </div>
          </div>
          <div class="cardT">
            <div>
              <div>火爆：{{zlcList.pet.petHot}}</div>
              <div>已助力：{{zlcList.pet.petHasHelp}}</div>
              <div>有助力：{{zlcList.pet.petHaveHelp}}</div>
            </div>
            <div>
              <el-button
                type="primary"
                :disabled="zlcList.pet.run"
                :loading="petLoading"
                @click="petRun"
              >开始助力</el-button>
              <el-button type="success" @click="reset(1)">火爆重置</el-button>
            </div>
          </div>
        </el-card>
        <el-card class="box-card" shadow="always">
          <div slot="header" class="clearfix">
            <div>种豆得豆</div>
            <div v-if="zlcList.plant.run">
              <i class="el-icon-refresh" style="color:#66ff03"></i>运行中
            </div>
            <div v-else>
              <i class="el-icon-remove-outline" style="color:yellow"></i>未运行
            </div>
          </div>
          <div class="cardT">
            <div>
              <div>火爆：{{zlcList.plant.plantHot}}</div>
              <div>已助力：{{zlcList.plant.plantHasHelp}}</div>
              <div>有助力：{{zlcList.plant.plantHaveHelp}}</div>
            </div>
            <div>
              <el-button
                type="primary"
                :disabled="zlcList.plant.run"
                :loading="plantLoading"
                @click="plantRun"
              >开始助力</el-button>
              <el-button type="success" @click="reset(2)">火爆重置</el-button>
            </div>
          </div>
        </el-card>
      </div>

      <el-form :inline="true" :model="formInline" class="searchForm">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="ck">
              <el-input v-model="formInline.ck" placeholder="ck" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="等级">
              <el-select v-model="formInline.level" placeholder="请选择等级" clearable>
                <el-option
                  v-for="item in levelOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="状态">
              <el-select v-model="formInline.status" placeholder="请选择状态" clearable>
                <el-option
                  v-for="item in statusOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="备注">
              <el-input v-model="formInline.remark" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="20" style="text-align:right">
            <el-form-item>
              <el-button type="primary" @click="onSubmit">查询</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <el-table :data="tableData" v-loading="tableLoading" style="width: 100%" border>
        <el-table-column label="序号" width="80">
          <template slot-scope="scope">
            <span>{{(pageNo-1)*pageSize+scope.$index+1}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="ptPin" label="ptPin" width="150" align="center"></el-table-column>
        <el-table-column prop="remark" label="备注" width="250" align="center"></el-table-column>
        <el-table-column label="等级" width="100" align="center">
          <template slot-scope="scoped">{{ scoped.row.level | level }}</template>
        </el-table-column>
        <el-table-column label="农场状态" width="90" align="center">
          <template slot-scope="scoped">
            <span :class="scoped.row.fruitStataus==1 ?'rdot':'bdot'"></span>
            <!-- {{ scoped.row.fruitStataus==1 ? '火爆' : '正常' }} -->
          </template>
        </el-table-column>
        <el-table-column label="农场助力" width="90" align="center">
          <template slot-scope="scoped">{{ scoped.row.fruitHelp==1 ? '已满' : '未满' }}</template>
        </el-table-column>
        <el-table-column label="萌宠状态" width="90" align="center">
          <template slot-scope="scoped">
            <span :class="scoped.row.petStataus==1 ?'rdot':'bdot'"></span>
            <!-- {{ scoped.row.petStataus==1 ? '火爆' : '正常' }} -->
          </template>
        </el-table-column>
        <el-table-column label="萌宠助力" width="90" align="center">
          <template slot-scope="scoped">{{ scoped.row.petHelp==1 ? '已满' : '未满' }}</template>
        </el-table-column>
        <el-table-column label="种豆状态" width="90" align="center">
          <template slot-scope="scoped">
            <span :class="scoped.row.plantStataus==1 ?'rdot':'bdot'"></span>
            <!-- {{ scoped.row.plantStataus==1 ? '火爆' : '正常' }} -->
          </template>
        </el-table-column>
        <el-table-column label="种豆助力" width="90" align="center">
          <template slot-scope="scoped">{{ scoped.row.plantHelp==1 ? '已满' : '未满' }}</template>
        </el-table-column>
        <el-table-column prop="expiryTime" label="过期时间" width="100" align="center"></el-table-column>
        <el-table-column label="是否有效" width="100" align="center">
          <template slot-scope="scoped">{{ scoped.row.status | status }}</template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scoped">
            <el-button type="text" @click="addCk(2, scoped.row)">修改</el-button>
            <el-button type="text" :loading="scoped.row.delLoading" @click="deleteCK(scoped.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :page-sizes="[10, 20, 30]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableTotal"
      ></el-pagination>
    </div>

    <!-- 添加ck -->
    <el-dialog
      :title="ckName"
      :visible.sync="addVisible"
      ref="ckvis"
      width="30%"
      @close="addClose('addForm')"
    >
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="100px">
        <el-form-item label="京东ck" prop="ck">
          <el-input v-model="addForm.ck" placeholder="请输入ck" clearable></el-input>
        </el-form-item>
        <el-form-item label="level" prop="level">
          <el-select v-model="addForm.level" placeholder="请选择level" clearable>
            <el-option
              v-for="item in levelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input v-model="addForm.remark" placeholder="请输入备注" clearable></el-input>
        </el-form-item>
        <el-form-item label="过期时间" v-if="addForm.level=='1'" prop="expiryTime">
          <el-date-picker
            v-model="addForm.expiryTime"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addVisible = false">取 消</el-button>
        <el-button type="primary" :loading="sureLoading" @click="sureAdd('addForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import {
  addCk,
  getJdCks,
  updateJdCk,
  deleteJdCks,
  getZlcInfo,
  fruitShareHelp,
  petShareHelp,
  plantShareHelp,
  resetHot
} from "../axiosRequest/allRequest";

export default {
  filters: {
    level(value) {
      const obj = {
        0: "永久",
        1: "vip",
        2: "普通",
        3: "工具人"
      };
      return obj[value];
    },
    status(value) {
      const obj = {
        0: "有效",
        1: "无效"
      };
      return obj[value];
    }
  },
  data() {
    return {
      formInline: {},
      tableData: [],
      ckName: "添加CK",
      addVisible: false,
      addForm: {
        ck: "",
        remark: "",
        level: "",
        expiryTime: "",
        id: ""
      },
      addRules: {
        ck: [{ required: true, message: "请输入ck", trigger: "blur" }],
        remark: [],
        level: [{ required: true, message: "请选择level", trigger: "blur" }],
        expiryTime: [],
        id: []
      },
      levelOptions: [
        {
          value: 0,
          label: "永久"
        },
        {
          value: 1,
          label: "vip"
        },
        {
          value: 2,
          label: "普通"
        },
        {
          value: 3,
          label: "工具人"
        }
      ],
      statusOptions: [
        {
          value: 0,
          label: "有效"
        },
        {
          value: 1,
          label: "无效"
        }
      ],
      sureLoading: false,
      tableLoading: false,
      pageNo: 1,
      pageSize: 10,
      tableTotal: 0,
      zlcList: {
        fruit: {},
        pet: {},
        plant: {}
      },
      zlcTimer: null,
      plantLoading: false,
      petLoading: false,
      fruitLoading: false
    };
  },

  created() {
    this.onSubmit();
    getZlcInfo().then(res => {
      if (res.code == 200) {
        this.zlcList = res.data;
      }
    });
    this.zlcTimer = setInterval(() => {
      getZlcInfo().then(res => {
        if (res.code == 200) {
          this.zlcList = res.data;
        }
      });
    }, 60 * 1000);
  },
  beforeDestroy() {
    clearInterval(this.zlcTimer);
  },
  methods: {
    addCk(i, row) {
      this.addVisible = true;
      if (i == 1) {
        this.ckName = "添加CK";
      } else {
        this.ckName = "修改CK";
      }

      if (row) {
        this.$nextTick(() => {
          this.addForm.ck = row.ck;
          this.addForm.remark = row.remark;
          this.addForm.level = row.level;
          this.addForm.expiryTime = row.expiryTime;
          this.addForm.id = row.id;
        });
      }
    },

    sureAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.sureLoading = true;
          let param = this.addForm;
          if (this.addForm.level != 1) {
            delete param.expiryTime;
            // delete param.id
          }
          if (this.ckName == "添加CK") {
            addCk(param).then(res => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("添加成功");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          } else {
            updateJdCk(param).then(res => {
              this.sureLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("修改成功");
                this.addVisible = false;
                this.onSubmit();
              }
            });
          }
        } else {
          return false;
        }
      });
    },

    addClose(formName) {
      this.$refs[formName].resetFields();
    },

    onSubmit() {
      this.tableLoading = true;
      let param = {
        ...JSON.parse(JSON.stringify(this.formInline)),
        pageNo: this.pageNo,
        pageSize: this.pageSize
      };
      getJdCks(param).then(res => {
        this.tableLoading = false;
        if (res.code != 200) {
          Message.error(res.message);
        } else {
          let a = JSON.parse(JSON.stringify(res.data.jdCkList));
          a.forEach((item, index) => {
            this.$set(item, "delLoading", false);
          });
          this.tableData = a;
          this.tableTotal = res.data.total;
        }
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
      this.onSubmit();
    },
    handleCurrentChange(val) {
      this.pageNo = val;
      this.onSubmit();
    },

    deleteCK(row) {
      this.$confirm("确认删除此CK吗？", "删除！", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.tableData.forEach(item => {
          if (row.id == item.id) {
            this.$set(item, "delLoading", true);
          }
        });
        deleteJdCks([row.id]).then(res => {
          this.tableData.forEach(item => {
            if (row.id == item.id) {
              this.$set(item, "delLoading", false);
            }
          });
          if (res.code != 200) {
            Message.error(res.message);
          } else {
            Message.success("删除成功");
            this.onSubmit();
          }
        });
      });
    },

    fruitRun() {
      this.fruitLoading = true;
      fruitShareHelp().then(res => {
        this.fruitLoading = false;
        if (res.code == 200) {
          Message.success("东东农场开始助力");
          getZlcInfo().then(res => {
            if (res.code == 200) {
              this.zlcList = res.data;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
    petRun() {
      this.petLoading = true;
      petShareHelp().then(res => {
        this.petLoading = false;
        if (res.code == 200) {
          Message.success("东东萌宠开始助力");
          getZlcInfo().then(res => {
            if (res.code == 200) {
              this.zlcList = res.data;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
    plantRun() {
      this.plantLoading = true;
      plantShareHelp().then(res => {
        this.plantLoading = false;
        if (res.code == 200) {
          Message.success("种豆得豆开始助力");
          getZlcInfo().then(res => {
            if (res.code == 200) {
              this.zlcList = res.data;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    },
    reset(type) {
      resetHot(type).then(res => {
        if (res.code == 200) {
          Message.success("重置成功");
          getZlcInfo().then(res => {
            if (res.code == 200) {
              this.zlcList = res.data;
            }
          });
        } else {
          Message.error(res.message);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
::v-deep .searchForm {
  .el-form-item__label {
    width: 60px;
  }
}

.cardBox {
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
  .el-card {
    width: 33%;

    .cardT,
    .clearfix {
      display: flex;
      justify-content: space-around;
      div {
        flex: 1;
      }
      & > div:nth-child(2) {
        text-align: right;
      }
    }
  }
}

.rdot {
  display: inline-block;
  background: red;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  vertical-align: super;
}
.bdot {
  display: inline-block;
  background: #66ff03;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  vertical-align: super;
}
</style>